<template>
    <div>
        <h1>{{ preview.title }}</h1>
        <img :src="preview.image" class="center" />
        <h1>{{ servings }}</h1>
        <div class="preview">
          Ready in: {{ preview.readyInMinutes }} minutes <br>
          {{ preview.popularity }} 
          <img src="https://cdn.shopify.com/s/files/1/0531/4392/9023/products/DesignohneTitel_15_1000x.png?v=1610292476" height="50" width=50>
          <br>
          <span v-if="preview.vegan">
              <img src="https://upload.wikimedia.org/wikipedia/commons/5/5b/Vegan_friendly_icon.svg" height="50" width=50>
          </span>
          <span v-if="preview.vegetarian">
              <img src="https://t3.ftcdn.net/jpg/05/33/16/84/360_F_533168476_ZwcHp3VXYOAr8RaAHxt2p1FwT0TbXTzj.jpg" height="50" width=50>
          </span>
          <span v-if="preview.glutenFree">
              <img src="data:image/png;base64," height="50" width=50>
          </span>
          <span v-if="preview.favorite">
              <img src="data:image/jpeg;base64,">
          </span>
          <span v-if="preview.watched">
              <img src="data:image/png;base64,">
          </span>    
        </div>
        <div class="ingredients"> 
          <ul>
            <li v-for="ing in ingredients" :key="ing.name">
              {{ ing.name }}, {{ ing.quantity }}, {{ing.unit}}
            </li>
          </ul>
        </div>
        <div class="instructions"> 
          <ul>
            <li v-for="ins in instructions" :key="ins.id">
              {{ ins.step }}
            </li>
          </ul>
        </div>
      </div>




</template>

<script>
export default {
  name: "RecipeView",

  data() {
    return {
      ingredients: [],
      instructions: [],
      servings: 0,
      preview: null,
    };
  },
  async created() {
    try {
      let response;

      try {
        response = await this.axios.get(
          this.$root.store.server_domain + `/recipes/${this.$route.params.recipeId}`
        );
        if (response.status !== 200) this.$router.replace("/NotFound");
      } catch (error) {
        console.log("error.response.status", error.response.status);
        this.$router.replace("/NotFound");
        return;
      }
      this.ingredients = response.data.ingredients;
      this.servings = response.data.servings;
      this.preview = response.data.recipeDetails;
      let _instructions = response.data.instructions
      for (let i = 0; i < _instructions.length; i++)
      {
        this.instructions.push(
          {
            id: i + 1,
            step: _instructions[i]
          }
        )
      }


    } catch (error) {
      console.log(error);
    }
  }
};
</script>

<style scoped>
.wrapper {
  display: flex;
}
.wrapped {
  width: 50%;
}
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

</style>
